
"use client";

import { useState } from "react";
import { zodResolver } from "@hookform/resolvers/zod";
import { useForm } from "react-hook-form";
import * as z from "zod";
import { Button } from "@/components/ui/button";
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { Textarea } from "@/components/ui/textarea";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { useToast } from "@/hooks/use-toast";
import { analyzeSupplyChain, type AnalyzeSupplyChainOutput } from "@/ai/flows/supply-chain-analyzer";
import { Loader2, Zap, AlertTriangleIcon, Lightbulb, BarChartBig } from "lucide-react";

const formSchema = z.object({
  chaincodeEvents: z.string().min(10, "Los eventos de chaincode deben tener al menos 10 caracteres."),
  assetData: z.string().min(10, "Los datos de activos deben tener al menos 10 caracteres."),
});

const placeholderChaincodeEvents = `Ejemplo de Eventos (Vino):
Event: GrapeLotRegistered, AssetID: GRAPE-1A2B, Variety: Malbec, Timestamp: 2023-03-15T10:00:00Z
Event: TransferInitiated, AssetID: GRAPE-1A2B, From: Finca El Sol, To: Bodegas Mendoza, Timestamp: 2023-03-20T11:00:00Z
Event: TransferAccepted, AssetID: GRAPE-1A2B, By: Bodegas Mendoza, Timestamp: 2023-03-21T09:00:00Z
Event: WineBatchRegistered, InputAssetID: GRAPE-1A2B, OutputAssetID: WINE-3C4D, Name: Reserva Malbec, Timestamp: 2023-04-10T15:00:00Z
...`;

const placeholderAssetData = `Ejemplo de Datos de Activos (Vino):
AssetID: GRAPE-1A2B, Name: Cosecha Malbec 2023, Origin: Mendoza, Argentina
AssetID: WINE-3C4D, Name: Reserva Malbec 2023, GrapeLots: [GRAPE-1A2B], Bodega: Bodegas Mendoza
...`;


export function SupplyChainAnalyzerClient() {
  const { toast } = useToast();
  const [analysisResult, setAnalysisResult] = useState<AnalyzeSupplyChainOutput | null>(null);
  const [isLoading, setIsLoading] = useState(false);

  const form = useForm<z.infer<typeof formSchema>>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      chaincodeEvents: "",
      assetData: "",
    },
  });

  async function onSubmit(values: z.infer<typeof formSchema>) {
    setIsLoading(true);
    setAnalysisResult(null);
    try {
      const result = await analyzeSupplyChain(values);
      setAnalysisResult(result);
      toast({
        title: "Análisis Completo",
        description: "El análisis de la cadena de suministro se ha generado correctamente.",
      });
    } catch (error) {
      console.error("Error en Análisis IA:", error);
      toast({
        title: "Falló el Análisis",
        description: "Ocurrió un error al analizar la cadena de suministro. Por favor, inténtalo de nuevo.",
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  }

  return (
    <div className="space-y-8">
      <Form {...form}>
        <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
          <FormField
            control={form.control}
            name="chaincodeEvents"
            render={({ field }) => (
              <FormItem>
                <FormLabel className="text-lg font-semibold">Datos de Eventos de Chaincode</FormLabel>
                <FormControl>
                  <Textarea
                    placeholder={placeholderChaincodeEvents}
                    className="min-h-[150px] resize-y font-code text-sm"
                    {...field}
                  />
                </FormControl>
                <FormDescription>
                  Pega los eventos de la red de Hyperledger Fabric.
                </FormDescription>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name="assetData"
            render={({ field }) => (
              <FormItem>
                <FormLabel className="text-lg font-semibold">Datos de Lotes Registrados</FormLabel>
                <FormControl>
                  <Textarea
                    placeholder={placeholderAssetData}
                    className="min-h-[150px] resize-y font-code text-sm"
                    {...field}
                  />
                </FormControl>
                <FormDescription>
                  Provee datos sobre los lotes de uva y vino registrados en el sistema.
                </FormDescription>
                <FormMessage />
              </FormItem>
            )}
          />
          <Button type="submit" className="w-full sm:w-auto text-base py-3 px-6" disabled={isLoading}>
            {isLoading ? (
              <>
                <Loader2 className="mr-2 h-5 w-5 animate-spin" /> Analizando...
              </>
            ) : (
              <>
                <Zap className="mr-2 h-5 w-5" /> Analizar Cadena de Suministro
              </>
            )}
          </Button>
        </form>
      </Form>

      {isLoading && (
         <div className="flex justify-center items-center py-10">
          <Loader2 className="h-12 w-12 animate-spin text-primary" />
          <p className="ml-4 text-lg text-muted-foreground">La IA está procesando tus datos...</p>
        </div>
      )}

      {analysisResult && !isLoading && (
        <div className="mt-10 space-y-6">
          <h2 className="font-headline text-2xl text-center mb-6 flex items-center justify-center gap-2">
             <BarChartBig className="h-7 w-7 text-primary"/> Resultados del Análisis IA
          </h2>
          <Card className="border-yellow-500 shadow-lg">
            <CardHeader className="bg-yellow-50 text-yellow-800 rounded-t-lg">
              <CardTitle className="flex items-center gap-2 font-headline">
                <AlertTriangleIcon className="h-6 w-6 text-yellow-600" /> Cuellos de Botella Identificados
              </CardTitle>
            </CardHeader>
            <CardContent className="pt-4">
              <p className="whitespace-pre-wrap">{analysisResult.bottlenecks}</p>
            </CardContent>
          </Card>

          <Card className="border-red-500 shadow-lg">
            <CardHeader className="bg-red-50 text-red-800 rounded-t-lg">
              <CardTitle className="flex items-center gap-2 font-headline">
                <Zap className="h-6 w-6 text-red-600" /> Disrupciones Potenciales
              </CardTitle>
            </CardHeader>
            <CardContent className="pt-4">
              <p className="whitespace-pre-wrap">{analysisResult.disruptions}</p>
            </CardContent>
          </Card>

          <Card className="border-green-500 shadow-lg">
            <CardHeader className="bg-green-50 text-green-800 rounded-t-lg">
              <CardTitle className="flex items-center gap-2 font-headline">
                <Lightbulb className="h-6 w-6 text-green-600" /> Optimizaciones Sugeridas
              </CardTitle>
            </CardHeader>
            <CardContent className="pt-4">
              <p className="whitespace-pre-wrap">{analysisResult.optimizations}</p>
            </CardContent>
          </Card>
        </div>
      )}
    </div>
  );
}
